
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'Login',
  computed: {
    vertrouwd() {
      return !!this.$store.state.user.locatieToken;
    },
  },
  created(): void {
    if (this.$store.state.user.tokenData) {
      this.$router.push('/personen');
    }
  },
});
